import React from 'react';
import { Link } from 'react-router-dom';
import user from 'assets/images/user.png';
import earn from 'assets/images/earn.png';

const HLoggedin = () => {
  return (
    <ul className="partner-items d-flex align-items-center">
      <li className="point-group"><img src={earn} alt="earn" /> 250</li>
      <li>
        <div className="user-login">
          <img src={user} alt="user" />
          <h4>Hi, Emily!</h4>
        </div>

      </li>
      <li>
        <div className="dropdown">
          <button className="dropbtn"></button>
          <div className="dropdown-content">
            <Link to="/myaccount/userprofile">User profile</Link>
            <Link to="/myaccount/ftsplaypoints">FTS Play Points <span><img src={earn} alt="earn" /> 250</span></Link>
            <Link to="/myaccount/history">History</Link>
            <Link to="#">Clubs</Link>
            <Link to="#">Refer and earn</Link>
            <Link to="#">Support</Link>
            <Link to="#">Terms and Policies</Link>
          </div>
        </div>
      </li>
    </ul>
  );
};

export default HLoggedin;