import React from "react";
import vector2 from 'assets/images/vector-2.svg';
import { Link } from 'react-router-dom';
import HLoggedin from './HLoggedin';

class HMBottom extends React.Component {
  render() {
    const { url, logged } = this.props;
    const hurl = (url !== undefined) ? url : '/';
    return (
      <div className="d-none d-lg-block bg-black-primary text-white">

        <div className="container-fluid">

          <div className="d-md-flex justify-content-between align-items-center">

            <div className="header-logo d-flex justify-content-center align-items-center nav-header-margin barherader">

              <div className="offers-badge mr-2">
                <img src={vector2} alt="vector-2" />
                <div className="vector-txt">Offers</div>
              </div>


              <Link to={hurl}>
                <button
                  type="button"
                  className="btn btn-primary partner skew-rotate fix-button-height partnerHeaderButton"
                >
                  <span className="remove-skew-rotate">Partner With Us</span>
                </button>
              </Link>


              <Link to={hurl}>
                <button type="button" className="btn btn-primary btnclubs">
                  <span>Clubs</span>
                </button>
              </Link>

            </div>


            <div>
              {
                logged && true ? <HLoggedin /> :
                  (
                    <ul className="partner-items d-flex align-items-center">
                      <li>
                        <Link to="/login" data-ajax="false">
                          <button
                            className="btn btn-outline-primary mr-2"
                          >
                            Log in
                          </button>
                        </Link>
                      </li>
                      <li>
                        <Link to="/signup" data-ajax="false">
                          <button className="btn btn-orange text-black">
                            Sign up
                        </button>
                        </Link>
                      </li>
                    </ul>
                  )
              }
            </div>

          </div>

        </div>

      </div>
    );
  }
}

export default HMBottom;
