import React, { Component } from 'react';
import Header from "components/Header";
import Footer from "components/Footer";
import FooterBottom from "components/Footer/FooterBottom";
import Sulimiter from './Sulimiter';
import SuOTPModal from './SuOTPModal';
import SuSuccessModal from './SuSuccessModal';

class SignUp extends Component {
  constructor(props) {
    super(props);
    this.state = {
      url: 'login'
    }
  }
  
  render() {
    // const {url} = this.state;
    // console.log("a");
    return (
      <div>
        <Header url="login" />
        <Sulimiter />
        <Footer />
        <FooterBottom />
        <SuOTPModal />
        <SuSuccessModal />
      </div>
    );
  }
}

export default SignUp;